import axios from "axios";
import Base from "./base";

class Experiences extends Base {
  constructor() {
    super();
  }
}

const state = {
  experiences: new Experiences(),
};

const getters = {
  experiences: (state) => state.experiences,
};

const actions = {
  async fetchExperiences({ commit, state }) {
    commit("setLoadingExperiences", true);
    const res = await axios.get(
      "https://agungsepruloh.herokuapp.com/experiences"
    );
    commit("setLoadingExperiences", false);
    commit("setExperiences", res.data);
  },
};

const mutations = {
  setExperiences: (state, experiences) => (state.experiences = experiences),
  setLoadingExperiences: (state, value) =>
    (state.experiences.isLoading = value),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
