import axios from "axios";
import Base from "./base";

class Profile extends Base {
  constructor() {
    super();
    this.address = {};
  }
}

const state = {
  profile: new Profile(),
};

const getters = {
  profile: (state) => state.profile,
};

const actions = {
  async fetchProfile({ commit }) {
    commit("setLoadingProfile", true);
    const res = await axios.get("https://agungsepruloh.herokuapp.com/profile");
    commit("setLoadingProfile", false);
    commit("setProfile", res.data);
  },
};

const mutations = {
  setProfile: (state, profile) => (state.profile = profile),
  setLoadingProfile: (state, value) => (state.profile.isLoading = value),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
