import axios from "axios";
import Base from "./base";

class Awards extends Base {}

const state = {
  awards: new Awards(),
};

const getters = {
  awards: (state) => state.awards,
};

const actions = {
  async fetchAwards({ commit }) {
    commit("setLoadingAwards", true);
    const res = await axios.get("https://agungsepruloh.herokuapp.com/awards");
    commit("setLoadingAwards", false);
    commit("setAwards", res.data);
  },
};

const mutations = {
  setAwards: (state, awards) => (state.awards = awards),
  setLoadingAwards: (state, value) => (state.awards.isLoading = value),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
