<template>
  <section class="resume-section" id="education">
    <div class="resume-section-content">
      <h2 class="mb-5">Education</h2>
      <div
        class="d-flex flex-column flex-md-row justify-content-between mb-5"
        v-if="educations.isLoading"
      >
        <ListLoader />
      </div>

      <div
        class="d-flex flex-column flex-md-row justify-content-between mb-5"
        v-else
        v-for="education in educations"
        :key="education.id"
      >
        <div class="flex-grow-1">
          <h3 class="mb-0">{{ education.school }}</h3>
          <div class="subheading mb-3">{{ education.major }}</div>
          <!-- <div>Computer Science - Web Development Track</div> -->
          <p>{{ education.gpa }}</p>
        </div>
        <div class="flex-shrink-0">
          <span class="text-primary"
            >{{ education.since }} - {{ education.untill }}</span
          >
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { ListLoader } from "vue-content-loader";

export default {
  components: { ListLoader },
  computed: mapGetters(["educations"]),
  methods: mapActions(["fetchEducations"]),
  created() {
    this.fetchEducations();
  },
};
</script>
