<template>
  <section class="resume-section" id="experience">
    <div class="resume-section-content">
      <h2 class="mb-5">Experience</h2>
      <div
        class="d-flex flex-column flex-md-row justify-content-between mb-5"
        v-if="experiences.isLoading"
      >
        <ListLoader />
      </div>

      <div
        class="d-flex flex-column flex-md-row justify-content-between mb-5"
        v-else
        v-for="experience in experiences"
        :key="experience.id"
      >
        <div class="flex-grow-1">
          <h3 class="mb-0">{{ experience.occupation }}</h3>
          <div class="subheading mb-3">{{ experience.company }}</div>
          <p></p>
        </div>
        <div class="flex-shrink-0">
          <span class="text-primary"
            >{{ experience.since }} - {{ experience.untill }}</span
          >
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { ListLoader } from "vue-content-loader";

export default {
  components: { ListLoader },
  computed: mapGetters(["experiences"]),
  methods: mapActions(["fetchExperiences"]),
  created() {
    this.fetchExperiences();
  },
};
</script>
