import axios from "axios";

const state = {
  socialMedia: [],
};

const getters = {
  socialMedia: (state) => state.socialMedia,
};

const actions = {
  async fetchSocialMedia({ commit }) {
    const res = await axios.get(
      "https://agungsepruloh.herokuapp.com/social-media"
    );
    commit("setSocialMedia", res.data);
  },
};

const mutations = {
  setSocialMedia: (state, socialMedia) => (state.socialMedia = socialMedia),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
