import axios from "axios";
import Base from "./base";

class Interests extends Base {}

const state = {
  interests: new Interests(),
};

const getters = {
  interests: (state) => state.interests,
};

const actions = {
  async fetchInterests({ commit }) {
    commit("setLoadingInterests", true);
    const res = await axios.get(
      "https://agungsepruloh.herokuapp.com/interests"
    );
    commit("setLoadingInterests", false);
    commit("setInterests", res.data);
  },
};

const mutations = {
  setInterests: (state, interests) => (state.interests = interests),
  setLoadingInterests: (state, value) => (state.interests.isLoading = value),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
